.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

.navbar-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  line-height: 1;
  border-bottom: 1px solid #e8e8e8;
}

.navbar-container .ant-menu {
  border: none;
}

.navbar-container ul li {
  font-family: 'Raleway';
}

.navbar-container .navbar-brand {
  padding: 0;
}

.navbar-container .navbar-brand img {
  width: 75px;
}

.navbar-container .social-icons {
  padding-right: 0;
  padding-left: 5px;
  font-size: 1.2em;
}

.navbar-container a.linkOut {
  color: #132149;
}

.navbar-container a.linkOut:hover,
.navbar-container a.linkOut:active,
.navbar-container a.linkOut:visited {
  text-decoration: none;
  color: #132149;
}

.navbar-container a.linkOut .main-site {
  background-image: url("../../assets/images/THP_logo_circle.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  display: inline-block;
}

@media (min-width: 992px) {
  .navbar-container {
    padding: 0em 1em;
    line-height: 1;
  }
}

.navbar-container .navbar-brand {
  font-size: 2em;
  line-height: 1.45;
}

@media (min-width: 1600px) {
  .navbar-container {
    padding: 0.25em 1em 0.15em 1em;
  }
}

.navbar-container .navbar-brand {
  font-size: 1.85em;
  line-height: 1.55;
}

.navbar-container a.linkOut {
  font-size: 1.3em;
}

@media (min-width: 1700px) {
  .navbar-container {
    padding: 0.2em 1em 0.1em 1em;
  }
}

.navbar-container .navbar-brand {
  font-size: 1.5em;
  line-height: 1.35;
}

.navbar-container a.linkOut {
  font-size: 1.15em;
}

