.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

.header-container {
  font-family: 'Raleway';
}

.header-container .header-pledge--CTA {
  flex: 2;
  display: flex;
  overflow: hidden;
  position: relative;
  align-content: end;
  justify-content: flex-end;
  background-size: cover;
  background-repeat: no-repeat;
  font-size: 1rem;
}

.header-container .spacer {
  flex: 1;
}

@media screen and (max-width: 992px) {
  .header-container .spacer {
    display: none;
  }
}

.header-container img {
  position: absolute;
  height: 100%;
  left: 0;
}

.header-container .do-your-job-banner {
  border-bottom: #faad14 2px solid;
}

.header-container .text-container {
  flex: 2;
  z-index: 1;
  padding: 2em;
  background: #ffffffe0;
  color: #132149;
}

.header-container .text-container h1 {
  text-transform: uppercase;
  color: #3facef;
  font-weight: 900;
  line-height: 1em;
  font-size: 3em;
}

.header-container .text-container h2 {
  color: #3facef;
  font-weight: 600;
  line-height: 1em;
  font-size: 1.5em;
}

.header-container .text-container p {
  font-size: 1.4em;
  line-height: 1.25;
}

.header-container .text-container button {
  color: #132149;
  text-align: right;
  float: right;
  font-weight: 600;
}

