.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

div.header-pledge--count {
  display: flex;
  text-align: center;
  color: #ffffff;
  line-height: 1;
}

div.header-pledge--count div {
  padding: 0.5em 0;
  overflow: hidden;
}

div.header-pledge--count .democrat {
  background: #66bdf4;
  flex-basis: 20.83%;
}

div.header-pledge--count .democrat-candidate {
  background: #96d2f7;
  flex-basis: 29.17%;
}

div.header-pledge--count .independent {
  background: #a4a2a4;
  flex-basis: 2.08%;
}

div.header-pledge--count .independent-candidate {
  background: #bdbcbd;
  flex-basis: 2.08%;
}

div.header-pledge--count .republican {
  background: #f87773;
  flex-basis: 16.67%;
}

div.header-pledge--count .republican-candidate {
  background: #faa6a4;
  flex-basis: 29.17%;
}

