.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

.search-bar {
  width: 100% !important;
}

.search-bar>div {
  width: 100% !important;
  flex: 1 100%;
  box-shadow: 0 0 3px rgba(34,25,25,0.4);
  padding: 1vw;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  background-color: #132149;
  z-index: 2000;
}

.search-bar>div h2 {
  flex: 1 100%;
  font-size: 1.6em;
  font-family: 'Raleway';
  text-align: center;
  color: #fff;
}

.search-bar>div p {
  color: #fff;
  font-family: 'Raleway';
  font-size: 1.4em;
  font-weight: 200;
  margin-bottom: 0;
  text-align: right;
}

.search-bar>div .ant-input-group-wrapper {
  flex: 1 50%;
  width: auto;
  max-width: 300px;
}

.search-bar>div .ant-input-group-wrapper .ant-btn.ant-input-search-button.ant-btn-primary {
  background-color: #ff4741;
  border-color: #ff4741;
}

.search-bar>div .input-group-filters {
  flex: 1 50%;
  display: flex;
  margin: auto;
  padding: 1vw;
}

@media all and (max-width: 778px) {
  .search-bar .ant-input-search.ant-input-affix-wrapper {
    flex: 1 100%;
  }

  .search-bar .input-group-filters {
    flex: 1 100%;
  }
}

.input-group__item {
  width: 25vw;
}

.text-input {
  width: 17vw;
  height: 25px;
}

.ant-tooltip-content {
  display: none;
}

