.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

.map-legend {
  left: 0;
  bottom: 0;
  z-index: 90;
  padding: 0.5rem 1.5rem 0.5rem;
  background-color: rgba(255,255,255,0.75);
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
}

.map-legend {
  display: flex;
}

.map-legend dt,
.map-legend dd {
  display: inline-block;
}

.map-legend dd {
  display: inline-block;
}

.map-legend dt {
  margin-right: 0.5rem;
  vertical-align: text-bottom;
}

.map-legend li {
  margin-right: 1.5rem;
  display: inline-block;
}

.map-legend .list-inline {
  list-style: none;
  margin-bottom: 0px;
  display: inline-block;
  padding-left: 0;
  flex: 10;
}

.map-legend .statewide-dyjd {
  border: 1px solid #faad14;
  width: 15px;
  height: 15px;
  background-color: none;
}

.map-legend .statewide-pledger {
  border: 1px solid #554d91;
  width: 15px;
  height: 15px;
  background-color: none;
}

.map-legend .district-dyjd {
  border: 1px solid #faad14;
  width: 15px;
  height: 15px;
  background-color: #faad14;
}

.map-legend .district-pledger {
  border: 1px solid #8982c2;
  width: 15px;
  height: 15px;
  background-color: #8982c2;
}

.map-legend .mayor-pledger {
  border: 1px solid #8982c2;
  width: 15px;
  height: 15px;
  background-color: #8982c2;
  border-radius: 50%;
}

.map-legend .missing-member-senate {
  border: 1px solid #b14038;
  width: 15px;
  height: 15px;
  background-color: none;
}

.map-legend .missing-member-district {
  border: 1px solid #e1a5a1;
  width: 15px;
  height: 15px;
  background-color: #e1a5a1;
}

