.sign-pledge-container {
  font-family: 'Raleway';
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
}

.sign-pledge-container p {
  font-size: 1.2em;
  flex: 1 100%;
}

.sign-pledge-container .download-button {
  font-size: 1.4em;
  margin: auto;
  flex: 1;
  text-align: center;
}

@media (max-width: 992px) {
  .sign-pledge-container .download-button {
    flex: 1 100%;
    margin-bottom: 0.5em;
  }
}

