@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,600,900");

.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

body {
  font-family: 'Raleway';
}

.h1,
h1 {
  font-size: 2.5rem;
}

h2,
h3 {
  margin: 10px;
  font-size: 1.2em;
}

h3 {
  font-size: 1em;
}

p {
  font-size: 0.85em;
  margin: 10px;
  text-align: left;
}

.hidden {
  display: none !important;
}

.background-light {
  background: #d4e0e5;
  color: #132149;
}

.background-light h1 {
  color: #0d4668;
}

.background-dark {
  background: #0d4668;
  color: #fff;
}

h1.background-dark {
  text-align: center;
  padding: 0.25em 0;
}

.do-your-job-icon {
  background-image: url("../../assets/images/do-your-job-logo.svg");
  height: 80px;
  background-size: 80px 80px;
  background-repeat: no-repeat;
  display: inline-block;
  width: 80px;
  vertical-align: middle;
}

.missing-member-icon {
  background-image: url("../../assets/images/missing-member-logo.svg");
  height: 50px;
  background-size: 50px 50px;
  background-repeat: no-repeat;
  width: 50px;
  vertical-align: middle;
  float: left;
}

.do-your-job-icon-small {
  background-image: url("../../assets/images/do-your-job-logo.svg");
  height: 25px;
  background-size: 25px 25px;
  background-repeat: no-repeat;
  width: 25px;
  vertical-align: middle;
  float: left;
}

.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.pl-5,
.px-5 {
  padding-left: 3rem !important;
}

.pr-5,
.px-5 {
  padding-right: 3rem !important;
}

.pb-4,
.py-4 {
  padding-bottom: 1.5rem !important;
}

.pt-4,
.py-4 {
  padding-top: 1.5rem !important;
}

.col-12 {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
  max-width: 100%;
}

