  position: relative;
  width: 100%;
  min-height: 1px;
}

@media (min-width: 992px) {
  .mb-lg-5,
  .my-lg-5 {
    margin-bottom: 3rem !important;
  }

  .mt-lg-5,
  .my-lg-5 {
    margin-top: 3rem !important;
  }

  .pl-5,
  .px-5 {
    padding-left: 3rem !important;
  }

  .pr-5,
  .px-5 {
    padding-right: 3rem !important;
  }

  .pb-4,
  .py-4 {
    padding-bottom: 1.5rem !important;
  }

  .pt-4,
  .py-4 {
    padding-top: 1.5rem !important;
  }
}

@media (min-width: 992px) {
  .col-lg-7 {
    -webkit-box-flex: 0;
  }
}

#table--state {
  flex-direction: column;
  display: flex;
  align-items: stretch;
  order: 2;
  flex: auto 2;
  margin: auto;
}

#table--state .total-pledged {
  float: right;
}

#table--state .pledge-status.pledged {
  color: #554d91;
}

#table--state .ant-badge {
  color: #554d91;
}

#table--state .ant-card-grid {
  padding-left: 10px;
  padding-right: 10px;
}

#table--state .no-results {
  margin-top: 50%;
}

#table--state ul {
  list-style: none;
  padding-left: 0px;
}

#table--state .state-card {
  margin-bottom: 4em;
}

#table--state .state-card .ant-card-head {
  border-top: 1px solid #e8e8e8;
  border-bottom: none;
  background-color: #fff;
  color: #132149;
  padding-left: 10px;
  padding-right: 10px;
}

#table--state .state-card .ant-card-head>.ant-card-head-wrapper>.ant-card-head-title {
  color: #132149;
  font-size: 2em;
  font-weight: 800;
  white-space: initial;
}

#table--state .state-card>.ant-card-body {
  padding: 10px 10px;
  display: flex;
  justify-content: space-evenly;
  flex-flow: row wrap;
}

#table--state .state-card>.ant-card-body .district-card {
  flex: 1 25%;
  margin: 0.5em;
}

#table--state .state-card>.ant-card-body .district-card .ant-card-head {
  background-color: #fff;
  border-top: none;
  border-bottom: 0.5px solid #d4e0e5;
  padding-left: 10px;
  padding-right: 10px;
}

#table--state .state-card>.ant-card-body .district-card .ant-card-head>.ant-card-head-wrapper>.ant-card-head-title {
  color: #132149;
  font-size: 1.2em;
  font-weight: 400;
}

#table--state .state-card>.ant-card-body .district-card .ant-card-head>.ant-card-head-wrapper>.ant-card-extra {
  color: #8982c2;
  text-transform: uppercase;
}

#table--state .state-card>.ant-card-body .district-card>.ant-card-body {
  padding: 10px 24px 10px 10px;
}

@media all and (max-width: 778px) {
  #table--state .state-card>.ant-card-body .district-card {
    margin: auto;
    flex: 1 100%;
  }
}

